
import { defineComponent } from 'vue'
import MainHeader from '@/components/MainHeader.vue'
import Sidebar from '@/components/Sidebar.vue'
import TaskBorderCard from '@/components/TaskBorderCard.vue'
import Overlay from '@/components/UI/Overlay.vue'

export default defineComponent({
  components: {
    MainHeader,
    Sidebar,
    Overlay,
    TaskBorderCard
  },
  data() {
    return {
      showModal: false,
      showAlert: false,
      tasks: { id: 1, completedTask: 372, openTasks: 9 }
    }
  },
  methods: {
    showOverlay(visible: boolean) {
      this.showModal = visible
    },
    hideOverlay(visible: boolean) {
      this.showModal = visible
    },
    changeMessage() {
      this.showAlert = true
    },
    changeTaskNumber() {
      if (this.tasks.openTasks !== 0) {
        this.tasks.completedTask = this.tasks.completedTask + 1
        this.tasks.openTasks = this.tasks.openTasks - 1
        if (this.tasks.openTasks === 0) {
          this.changeMessage()
        }
      }
    }
  }
})
